@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhai+2&display=swap);
.title-wrapper {
  min-width: 35vw;
  min-height: 15.5vh;
  padding: 3rem 1rem;
  animation: sliding-background 40s linear infinite;
  background: url(../../static/media/text-img.0c698ca7.png) no-repeat left / 120%;
  text-align: center;
}

.title {
  padding: 0.5rem;
  background: rgba(241, 243, 248,0.9);
  color: #000;
  font-family: "Montserrat", sans-serif;
  font-size: 3.5em;
  letter-spacing: 0.1em;
  line-height: 1.3em;
  margin: 0;
  mix-blend-mode: lighten;
  text-transform: uppercase;
  text-shadow: 0 0 0px, 35 #000;
  text-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 
  1px -1px 0 #000, -1px 1px 0 #000, 
  1px 1px 0 #000;
  border-radius: 3px;
}

.title:before {
  /* padding-left: 0.5rem; */
  color: #fff;
  content: attr(dataText);
  margin-left: -0.5%;
  margin-top: -0.5%;
  position: absolute;
}

@keyframes sliding-background {
  0%,
  100% {
    background-position: 0 40%;
  }

  50% {
    background-position: 100% 40%;
  }
}

.cart {
    min-height: 78vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.cart h2 {
    margin: 2rem 0 1rem 0;
}

.cart-details {
    display: flex;
    margin: 2rem 0;
}

.cart-item {
    width: 38rem;
    margin-bottom: 1rem;
    padding: 1rem;
    border: 1px solid #ccc;
}

.cart-item h3 {
    margin-bottom: 1rem;
}

p {
    padding: 0.5rem 0;
}

.cart-actions {
    width: 8rem;
    display: flex;
    flex-direction: column;
}

.qty {
    width: 3rem;
    margin-left: 0.5rem;
}

.cart-summary {
    margin-left: 1rem;
}

.cart-summary  h4 {
    margin-bottom: 1rem;
    font-weight: bold;
}

.product {
    max-width: 80vw;
    display: flex;
    margin: 2rem auto;
    padding: 1rem;
    background-color: #f3f4f7;
    box-shadow: 1px 3px 9px 2px rgba(0,0,0,0.25);
}

.products h2 {
    padding-top: 2rem;
}

.product-info {
    width: 30rem;
}

.product-btns {
    align-self: end;
    margin-left: auto;
}

.product-img {
    min-width: 150px;
    height: 180px;
    margin: 0 1rem 1rem 1rem;
    object-fit: contain;
}

/* .product-img img {
    width: 100%;
} */

.product h3 {
    padding-bottom: 1.5rem;
}

.price {
    margin-top: 1rem;
}

/* single product */
.single-cart-product-container {
    height: 75vh;
}

.single-cart-product-container h2 {
    margin-top: 3rem;
}

.single-cart-product {
    width: 58rem;
    margin: 3rem auto;
}

.single-cart-product img {
    margin: 1rem;
    margin-top: 0;
}

.single-cart-product .price {
    margin-top: 0rem;
}

.add-cart {
    margin-top: 1.9rem;
} 

.auth {
    margin: 3rem auto;
    height: 68vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

form {
    width: 30rem;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

form fieldset {
    width: 20rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 3rem auto;
    padding: 1rem;
    border-radius: 0.3em;
}

form label {
    display: flex;
    flex-direction: column;
}

label input {
    margin: 0.5rem 0;
}

.auth-user {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 1.5em;
}

.auth-user h3 {
    padding: 2rem 0 1rem 0
}

.login-btn {
    background-color: #c6e4c6;
    margin-top: 1rem;
    padding: 0.5rem 2rem;
}

.logout-btn {
    background-color: #e7c9ab;
    margin-top: 2rem;
}

nav {
    padding: 0.9rem 0;
    background-color: #8d93ab;
    border-bottom: 1px solid #7f8088;
    box-shadow: 1px 1px 3px 1px rgba(0,0,0,0.25);
}

nav ul  {
    /* width: 100vw; */
    display: flex;
    justify-content: space-around;
    align-items: center;
}

nav ul li {
    list-style: none;
}

nav ul a {
    margin: 0 1rem;
    text-decoration: none;
    font-size: 1.05em;
    font-weight: bold;
    color: #393b44;
}

nav ul a:hover, .logo:hover {
    color: #b6e4b6 !important;
}

.active {
    color: #d6e0f0;
}

.logo {
    color: #f1f3f8 !important;
}

.logo-icon {
    font-size: 1.3rem;
  -webkit-text-stroke: 1px #393b44;
  color: #f1f3f8;
  text-shadow:
      3px 3px 0 #8d93ab,
     -1px -1px 0 #8d93ab,  
      1px -1px 0 #8d93ab,
      -1px 1px 0 #8d93ab,
      1px 1px 0 #8d93ab;
}

.nav-link-cart {
    display: flex;
}

.nav-link-cart p {
    align-self: start;
    padding: 0 0.5rem;
    align-self: start;
}

.nav-link-cart p:first-child {
    padding: 0;
}

footer {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    font-family: 'Ubuntu', sans-serif;
    letter-spacing: 0.03em;
    color: #f1f3f8;
    background-color: #393b44;
    box-shadow: 1px 0px 10px 2px rgba(0,0,0,0.25);
}

* {
    margin: 0;
}

body {
  margin: 0;
  font-family: 'Baloo Bhai 2', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #393b44;
  background-color: #f1f3f8;
}

.app h2 {
  text-align: center;
}

/* HOME */
.home {
  height: 82vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h1 {
  font-size: 3rem;
  -webkit-text-stroke: 1px #393b44;
  color: #f1f3f8;
  text-shadow:
      3px 3px 0 #8d93ab,
     -1px -1px 0 #8d93ab,  
      1px -1px 0 #8d93ab,
      -1px 1px 0 #8d93ab,
      1px 1px 0 #8d93ab;
}

.slogan p {
  margin-top: 1rem;
  font-size: 1.2em;
  font-weight: bold;
  color: #393b44;
  /* -webkit-text-stroke: 1px #393b44; */
}

.text {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.1em;
  font-weight: bold;
  letter-spacing: 0.06em;
  color: #f1f3f8;
  padding: 0 0.2rem;
  background-color:#9591b4;
}

.text-above {
  position: relative;
  top: 1.9rem;
  right: 6.3rem;
  border-top: 1px solid #7f8088;
}

.text-below {
  position: relative;
  top: -2.8rem;
  left: 6.4rem;
  border-bottom: 1px solid #7f8088;
}

/* BUTTONS */
.btns {
  margin-right: 0.3em;
  padding: 0.5rem;
  color: #393b44;
  border: 1px solid #393b44;
  border-radius: 0.2rem;
}

.btns:hover {
  box-shadow: 1px 3px 9px 2px rgba(0,0,0,0.25);
}

.add {
  background-color: #d6e0f0;
}

/* .view {
    background-color: #d6e0f0;
    background-color: #8d93ab;
} */

.remove {
  background-color: #e7c9ab;
  margin-top: 1rem;
}

.proceed {
  background-color: #c6e4c6;
  margin-top: 1rem;
}

.checkout {
  margin-top: 2.5rem;
  font-weight: bold;
  text-decoration: none;
  /* background-color: #c6e4c6; */
  background-color: #d6e0f0;
}

.bold {
    font-weight: bold;
}

/* NOT FOUND */
.not-found {
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

